import React, { useEffect } from "react";
import "./Dashboard.css";
import { Form, Button, FloatingLabel } from "react-bootstrap";
import { useState } from "react";
import Axios from "../config/axios";
import axios from "axios"

function Dashboard() {
  const [quest, setQuestions] = useState(["1"]);
  const [on, setOn] = useState(true);
  const [data, setData] = useState({
    subject_name: "",
    publication: "",
    class_no: "",
    chapter_no: "",
    chapter_name: "",
    question_type: "",
    questions: "",
    option1: "",
    option2: "",
    option3: "",
    option4: "",
  });
  const [dataUpload, setDataUpload] = useState({});
  const [finalDataEntry, setFinalDataEntry] = useState({});

  let {
    subject_name,
    publication,
    class_no,
    chapter_no,
    chapter_name,
    question_type,
    questions,
    option1,
    option2,
    option3,
    option4,
  } = data;

  const handleChange = (e) => {
    // console.log(e.target.value, "e value");
    setData({ ...data, [e.target.name]: [e.target.value].toString() });
  };

  const handleSubmit = (event) => {
    event.preventDefault();

  }
  const handleClick = async (e) => {
    try {

      if (Object.keys(dataUpload).length > 0) {
        const final = {...data}
        final.option1 = dataUpload.option1;
        final.option2 = dataUpload.option2;
        final.option3 = dataUpload.option3;
        final.option4 = dataUpload.option4;
        setFinalDataEntry(final);
        console.log(final,"inside click")
        if(final.option1===dataUpload.option1 && final.option2===dataUpload.option2){
          const addRow =await  Axios.post("/v1/dataEntry/questt/add",finalDataEntry)
          .then(res=> {
            if(res.data.error){
              console.log("something went wrong while uploading, submit again")
            }else {
              console.log(res.data,"data added successfully")
              setTimeout(function(){ window.location.reload() }, 2000);
            }
          
  
          })
          .catch(err=>{
            console.log(err)
          })
        }
        
      } else {
        setFinalDataEntry(data);
      }

     
    } catch(err) {
      console.log(err)
    }
  };

  const handleImage = async (e) => {
    try {
      const formData = new FormData();

      formData.append("option_image", e.target.files[0]);
      formData.append("type", `${e.target.name}`);

      // console.log(formData,"val")

      setData({ ...data, [e.target.name]: [e.target.value].toString() });

      // console.log(e.target.files[0], "data value files");
      // console.log(e.target.name, "data value name");

      const loadData = await axios
        .post("https://isp.taskmo.in/quest/upload_option_image", formData)
        .then((response) => {
          // console.log(response.data.path, "data res");
          // console.log(response.data.type, "data res");
          setDataUpload({
            ...dataUpload,
            [response.data.type]: [response.data.path].toString(),
          });
        })
        .catch((err) => {
          console.log(err, "error");
        });
      return loadData;
    } catch (err) {
      console.log(err, "error");
    }
  };
  const handleClick1 = () => {
    setOn(true);
    setData({ ...data, option1: "", option2: "", option3: "", option4: "" });
  };
  const handleClick2 = () => {
    setOn(false);
    setData({ ...data, option1: "", option2: "", option3: "", option4: "" });
  };

  return (
    <div className="dashboard_outer">
      <div className="header">
        <img
          src={window.location.origin + "/images/logo/tmlogo.svg"}
          alt="taskmo-logo"
          className="logo_taskmo"
        />
      </div>
      <div className="body">
        {/* <h1 className="form_header">Questt Form</h1> */}
        {quest.map(() => (
          <Form className="dashboard" onSubmit={handleSubmit}>
            <h1 className="form_header">Questt Form</h1>
            <FloatingLabel
              className="mb-3"
              controlId="floatingSelectGrid"
              label="Subject"
              required
            >
              <Form.Select
                className="my-className"
                aria-label="Floating label select example"
                name="subject_name"
                value={subject_name}
                onChange={handleChange}
                required
              >
                <option value=""> Select option</option>
                <option value="English">English</option>
                <option value="Kannada">Kannada</option>
                <option value="Hindi">Hindi</option>
                <option value="EVS">EVS</option>
                <option value="Science">Science</option>
                <option value="Maths">Maths</option>
              </Form.Select>
            </FloatingLabel>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Publication</Form.Label>
              <Form.Control
                type="text"
                name="publication"
                value={publication}
                onChange={handleChange}
                placeholder="Enter Publication"
                required
              />
            </Form.Group>
            <FloatingLabel
              className="mb-3"
              controlId="floatingSelectGrid"
              label="class"
              required
            >
              <Form.Select
                aria-label="Floating label select example"
                name="class_no"
                value={class_no}
                onChange={handleChange}
                required
              >
                <option value="">Select option</option>
                <option value="1">1</option>
                <option value="2">2</option>
                <option value="3">3</option>
                <option value="4">4</option>
                <option value="5">5</option>
                <option value="6">6</option>
                <option value="7">7</option>
                <option value="8">8</option>
                <option value="9">9</option>
                <option value="10">10</option>
              </Form.Select>
            </FloatingLabel>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Chapter Number</Form.Label>
              <Form.Control
                type="number"
                placeholder="Desired Chapter No"
                name="chapter_no"
                value={chapter_no}
                onChange={handleChange}
                required
              />
            </Form.Group>
            <Form.Group className="mb-3" controlId="formBasicEmail">
              <Form.Label>Chapter Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Desired Chapter Name"
                name="chapter_name"
                value={chapter_name}
                onChange={handleChange}
                required
              />
            </Form.Group>
            <FloatingLabel
              className="mb-3"
              controlId="floatingSelectGrid"
              label="Type of question"
              required
            >
              <Form.Select
                aria-label="Floating label select example"
                name="question_type"
                value={question_type}
                onChange={handleChange}
                required
              >
                <option value="">Select option</option>
                <option value="MCQ with 1 correct option">
                  MCQ with 1 correct option
                </option>
                <option value="MCQ with multiple correct options">
                  MCQ with multiple correct options
                </option>
                <option value="True - False">True - False</option>
                <option value="One word / Fill up">One word / Fill up</option>
                <option value="Subjective">Subjective</option>
              </Form.Select>
            </FloatingLabel>
            <Form.Group
              className="mb-3"
              controlId="exampleForm.ControlTextarea1"
            >
              <Form.Label>Enter question</Form.Label>
              <Form.Control
                as="textarea"
                rows={3}
                name="questions"
                value={questions}
                onChange={handleChange}
                required
              />
            </Form.Group>
            <div className="switch_button">
              <Button
                className={on ? "file_button" : "text_button"}
                onClick={() => handleClick1()}
              >
                Text
              </Button>
              <Button
                className={on ? "text_button" : "file_button"}
                onClick={() => handleClick2()}
              >
                File Upload
              </Button>
            </div>
            {on ? (
              <Form.Group controlId="formTextMultiple" className="mb-3">
                <div className="row_dash">
                  <Form.Control
                    type="text"
                    placeholder="Option1"
                    name="option1"
                    value={option1}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="row_dash">
                  <Form.Control
                    type="text"
                    placeholder="Option2"
                    name="option2"
                    value={option2}
                    onChange={handleChange}
                    required
                  />
                </div>
                <div className="row_dash">
                  <Form.Control
                    type="text"
                    placeholder="Option3"
                    name="option3"
                    value={option3}
                    onChange={handleChange}
                    
                  />
                </div>
                <div className="row_dash">
                  <Form.Control
                    type="text"
                    placeholder="Option4"
                    name="option4"
                    value={option4}
                    onChange={handleChange}
                    
                  />
                </div>
                
              </Form.Group>
            ) : (
              <Form.Group controlId="formFileMultiple" className="mb-3">
                <Form.Label className="mt-3" required>
                  Option1
                </Form.Label>
                <Form.Check>
                  <Form.Control
                    type="file"
                    name="option1"
                    value={option1}
                    onChange={handleImage}
                    required
                  />
                  
                </Form.Check>
                <Form.Label className="mt-3" required>
                  Option2
                </Form.Label>
                <Form.Check>
                  <Form.Control
                    type="file"
                    name="option2"
                    value={option2}
                    onChange={handleImage}
                    required
                  />
                </Form.Check>
                <Form.Label className="mt-3" >
                  Option3
                </Form.Label>
                <Form.Check>
                  <Form.Control
                    type="file"
                    name="option3"
                    value={option3}
                    onChange={handleImage}
                    
                  />
                </Form.Check>
                <Form.Label className="mt-3" >
                  Option4
                </Form.Label>
                <Form.Check>
                  <Form.Control
                    type="file"
                    name="option4"
                    value={option4}
                    onChange={handleImage}
                    
                  />
                </Form.Check>
                
              </Form.Group>
            )}
            
            <div className="add_button">
              <Button
                className="button_add"
                variant="secondary"
                type="submit"
                onClick={() => handleClick()}
                
              >
                <img
                  src={window.location.origin + "/images/add.svg"}
                  alt="taskmo-add"
                  className="add"
                />
                <p className="taskmo_p1">Add question</p>
              </Button>
            </div>
          </Form>
        ))}
        <div className="buttons">
          <Button
            className="button_save"
            variant="primary"
            type="Download"
            onClick={() => {
              window.location.reload();
            }}
          >
            Download
          </Button>
        </div>
      </div>
    </div>
  );
}

export default Dashboard;

